
body{background:#f3f3f3
}
#app{font-family:Helvetica, Arial, sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;text-align:center;color:#333
}
input[type="text"]{padding:3px 4px;width:auto
}
.Grid{margin-top:2em;display:-ms-flexbox;display:flex
}
.Grid-cell{-ms-flex:1;flex:1;background-color:#f3f3f3
}
.Grid--gutters{margin:-1em 0 0 -1em
}
.Grid--gutters>.Grid-cell{padding:1em 0 0 1em
}
.col{padding:1em
}
.col h3{color:#666
}



